body, nav, ul, li, a {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.img {
  display: flex;
}

.list {
  justify-content: center;
  align-items: center;
  height: 60px;
  list-style: none;
  display: flex;
}

.link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.list-item:not(:last-child) {
  margin-right: 20px;
}

.link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

